<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Edit Required Attachments</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <div v-if="!loading">
                                    <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startRecordUpdate()">
                                        <!--[form-content-start]-->
                                        <div class="row q-col-gutter-md">
                                            <div class="col-12">
                                                <div class="row">
                                                    <div class="col-sm-3 col-12">
                                                        Application Id *
                                                    </div>
                                                    <div class="col-sm-9 col-12">
                                                        <api-data-source @loaded="response => mapOptionField(response, 'application_id')"  api-path="components_data/application_id_option_list"  v-slot="req">
                                                            <ValidationProvider :rules="{required:true}" name="Application Id" v-slot="{ errors, invalid, validated }">
                                                                <q-select   :loading="req.loading"  outlined dense  ref="ctrlapplication_id" emit-value map-options  v-model="formData.application_id" :options="req.response" label="Application Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                                <template v-slot:option="scope">
                                                                    <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                        </q-item-section>
                                                                        <q-item-section>
                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                            </q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    <template v-slot:selected-item="scope">
                                                                        <q-item  v-if="scope.opt">
                                                                            <q-item-section v-if="scope.opt.image" avatar>
                                                                                <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                            </q-item-section>
                                                                            <q-item-section>
                                                                                <q-item-label />{{ scope.opt.label }}
                                                                                    <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                </q-item-section>
                                                                            </q-item>
                                                                        </template>
                                                                        </q-select> 
                                                                    </ValidationProvider>
                                                                </api-data-source>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="col-12">
                                                        <div class="row">
                                                            <div class="col-sm-3 col-12">
                                                                Added By *
                                                            </div>
                                                            <div class="col-sm-9 col-12">
                                                                <api-data-source @loaded="response => mapOptionField(response, 'added_by')"  api-path="components_data/added_by_option_list"  v-slot="req">
                                                                    <ValidationProvider :rules="{required:true}" name="Added By" v-slot="{ errors, invalid, validated }">
                                                                        <q-select   :loading="req.loading"  outlined dense  ref="ctrladded_by" emit-value map-options  v-model="formData.added_by" :options="req.response" label="Added By"  :error="invalid && validated" :error-message="errors[0]" >
                                                                        <template v-slot:option="scope">
                                                                            <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                                <q-item-section v-if="scope.opt.image" avatar>
                                                                                    <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                </q-item-section>
                                                                                <q-item-section>
                                                                                    <q-item-label />{{ scope.opt.label }}
                                                                                        <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                    </q-item-section>
                                                                                </q-item>
                                                                            </template>
                                                                            <template v-slot:selected-item="scope">
                                                                                <q-item  v-if="scope.opt">
                                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                    </q-item-section>
                                                                                    <q-item-section>
                                                                                        <q-item-label />{{ scope.opt.label }}
                                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                        </q-item-section>
                                                                                    </q-item>
                                                                                </template>
                                                                                </q-select> 
                                                                            </ValidationProvider>
                                                                        </api-data-source>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div class="col-12">
                                                                <div class="row">
                                                                    <div class="col-sm-3 col-12">
                                                                        Note *
                                                                    </div>
                                                                    <div class="col-sm-9 col-12">
                                                                        <ValidationProvider :rules="{required:true}" name="Note" v-slot="{ errors, invalid, validated }">
                                                                            <q-input outlined dense  ref="ctrlnote" v-model="formData.note"  label="Note" type="text" placeholder="Enter Note"   list="note_list"  
                                                                            class="" :error="invalid && validated" :error-message="errors[0]">
                                                                            </q-input>
                                                                            <datalist id="note_list">
                                                                            <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                            </datalist>
                                                                        </ValidationProvider>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div class="col-12">
                                                                <div class="row">
                                                                    <div class="col-sm-3 col-12">
                                                                        Document Id *
                                                                    </div>
                                                                    <div class="col-sm-9 col-12">
                                                                        <ValidationProvider :rules="{required:true}" name="Document Id" v-slot="{ errors, invalid, validated }">
                                                                            <q-input outlined dense  ref="ctrldocument_id" v-model="formData.document_id"  label="Document Id" type="number" placeholder="Enter Document Id"   step="any" list="document_id_list"  
                                                                            class="" :error="invalid && validated" :error-message="errors[0]">
                                                                            </q-input>
                                                                            <datalist id="document_id_list">
                                                                            <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                            </datalist>
                                                                        </ValidationProvider>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <!--[form-content-end]-->
                                                        <div v-if="showSubmitButton" class="text-center q-my-md">
                                                            <q-btn    :rounded="false"  color="primary"  no-caps  unelevated   type="submit" :disabled="invalid" icon="send" :loading="saving">
                                                                <q-spinner-oval slot="loading" /> Update
                                                            </q-btn>
                                                        </div>
                                                    </ValidationObserver>
                                                    <slot :submit="submit" :saving="saving"></slot>
                                                </div>
                                                <div v-else class="q-pa-sm text-center">
                                                    <q-spinner
                                                    :size="40"
                                                    color="accent"
                                                    indeterminate>
                                                    </q-spinner>
                                                    Loading...
                                                </div>
                                            </div>
                                        </q-card>
                                    </div>
                                </div>
                            </div>
                        </section>
                    </div>
                </template>
                <script>
	import { PageMixin } from "../../mixins/page.js";
	import { EditPageMixin } from "../../mixins/editpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'editRequiredattachmentsPage',
		components: {
		},
		mixins: [PageMixin, EditPageMixin ],
		props: {
			pageName: {
				type: String,
				default: 'required_attachments',
			},
			idName: {
				type: String,
				default: 'id',
			},
			routeName: {
				type: String,
				default: 'required_attachmentsedit',
			},
			pagePath: {
				type : String,
				default : 'required_attachments/edit',
			},
			apiPath: {
				type: String,
				default: 'required_attachments/edit',
			},
		},
		data() {
            return {
				formData: {
					application_id: "", added_by: "", note: "", document_id: "", 
				},
        	}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Edit Required Attachments"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle // set browser page title
			}
		},
		methods: {
			...mapActions('required_attachments', ['updateRecord', 'fetchRecord']),
			async startRecordUpdate(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let id = this.id;
					let url = this.apiUrl;
					let payload = this.normalizedFormData();
					let data = { id, url, payload }
					this.updateRecord(data).then(
						(response) => {
							this.saving = false;
							this.flashMsg(this.msgAfterUpdate);
							this.resetForm();
							this.closeDialogs();// close page dialog that if opened
							this.navigateTo(`/required_attachments`);
						},
						(response) => {
							this.saving = false;
							this.showPageRequestError(response);
						}
					);
				}
			},
			updateFormFields: function(){
				//update form fields value after load from api
				//e.g convert fieldvalue (value,value2,value2) to array 
            },
			resetForm (){
				//reset form fields value
				this.formData = {application_id: "", added_by: "", note: "", document_id: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				//raise event to reset other custom form components
				this.$EventBus.$emit("resetForm");
			},
		},
	};
</script>
<style scoped>
</style>
